import { RequestLoginResponse } from './../models/requestlogin.model';
import { Injectable, NgZone } from '@angular/core';
import { BehaviorSubject, from, Observable } from 'rxjs';
import { AccountBalance } from '../models/accountBalance.model';
declare let ethereum: any;
declare let Web3: any;

@Injectable({
  providedIn: 'root',
})
export class MetamaskService {
  private minABI = [
    {
      constant: false,
      inputs: [
        {
          name: '_to',
          type: 'address',
        },
        {
          name: '_value',
          type: 'uint256',
        },
      ],
      name: 'transfer',
      outputs: [
        {
          name: 'success',
          type: 'bool',
        },
      ],
      payable: false,
      stateMutability: 'nonpayable',
      type: 'function',
    },
    {
      constant: true,
      inputs: [{ name: '_owner', type: 'address' }],
      name: 'balanceOf',
      outputs: [{ name: 'balance', type: 'uint256' }],
      type: 'function',
    },
    {
      constant: true,
      inputs: [],
      name: 'decimals',
      outputs: [{ name: '', type: 'uint8' }],
      type: 'function',
    },
  ];
  private contractAddress = '0xd71239a33c8542bd42130c1b4aca0673b4e4f48b';
  private decimals = 18;
  private web3;
  private contract;
  public currentAccount: BehaviorSubject<string> = new BehaviorSubject<string>('init');

  constructor(private ngZone: NgZone) {
    this.web3 = new Web3(ethereum);
    this.contract = new this.web3.eth.Contract(
      this.minABI,
      this.contractAddress
    );
    ethereum.on('chainChanged', this.handleChainChanged);
    ethereum.on('accountsChanged', (accounts: string[]) =>
      this.ngZone.run(() => {
        this.handleAccountsChanged(accounts);
      })
    );
  }

  handleAccountsChanged(accounts: string[]) {
    console.log("handleAccountsChanged"); 
    this.currentAccount.next(accounts[0]);
  }

  handleChainChanged(_chainId: number) {
    if (_chainId != 56) {
      alert('Please connect to BSC mainnet');
    }
  }

  openMetamask() {
    if (ethereum.selectedAddress) {
      this.currentAccount.next(ethereum.selectedAddress);
      return;
    }
    ethereum.request({
      method: 'eth_requestAccounts',
    });
  }

  async signMessage(request: RequestLoginResponse): Promise<string> {
    let message: string = `You are signing to login to CryptoRockets. OTP: ${request.noun}`;
    var promise: string = await this.web3.eth.personal.sign(
      message,
      ethereum.selectedAddress
    );
    return promise;
  }

  deposit(amount: number) {
    console.log(amount);
    this.contract.methods
      .transfer(
        '0x9D27fC37003a36e16fE413C5eF3ED6D225E322d4',
        amount * 10 ** 18 + ''
      )
      .send({
        from: this.currentAccount.value,
      })
      .then((data: any) => {
        console.log(data.transactionHash);
      });
  }

  async getAccountBalance(walletId: string): Promise<AccountBalance> {
    var tokenAmount = await this.getBalance(walletId);
    var bnbAmount = (await this.web3.eth.getBalance(walletId)) / (1 * 10 ** 18);

    return {
      walletId,
      bnbAmount,
      tokenAmount,
    };
  }

  private async getBalance(walletId: string): Promise<number> {
    return (
      (await this.contract.methods.balanceOf(walletId).call()) /
      (1 * 10 ** this.decimals)
    );
  }
}
