import { HeaderFacade } from 'src/app/services/header.facade';
import { GameSettingsService } from './gamesettings.service';
import { ListSharkResponse, Shark } from './../models/shark.model';
import { Observable, BehaviorSubject, Subject } from 'rxjs';
import { ApiService } from './api.service';
import { Injectable } from '@angular/core';
import { TokenAmountResponse } from '../models/accountBalance.model';
import { delay, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class SharksFacade {
  public $sharks: Observable<Shark[]> = new Observable<Shark[]>();
  public totalSharks: number = 0;
  public openedShark: Shark = {} as Shark;
  public $openedStatus: Subject<boolean> = new Subject<boolean>();
  public $buyClicked: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(
    false
  );

  public canBuy: boolean = false;
  public price: number = 0;

  constructor(
    private apiService: ApiService,
    private gameSettingService: GameSettingsService,
    private headerFacade: HeaderFacade
  ) {}

  public getListSharks() {
    this.$sharks = this.apiService.getSharks();
  }

  public onBuyButtonClick() {
    this.canBuy =
      this.headerFacade.currentAccount.tokenAmount >=
      this.gameSettingService.gameSettings.commonBoxPrice;
    this.price = this.gameSettingService.gameSettings.commonBoxPrice;
    this.$buyClicked.next(true);
  }

  public onBuyPopupClose(isOpen: boolean = false) {
    if (isOpen) {
      this.apiService.openBox().subscribe((openedShark) => {
        this.headerFacade.getTokenAmount();
        this.$openedStatus.next(true);
        this.openedShark = openedShark;
        this.$buyClicked.next(false);
      });
    }
  }

  public onOpenedSharkClose() {
    this.$openedStatus.next(false);
    this.getListSharks();
  }
}
