import { ApiService } from './api.service';
import { TokenHistoryResponse } from './../models/tokenHistory.model';
import { LocalStorageKey } from './../models/const';
import { AccountBalance } from './../models/accountBalance.model';
import { MetamaskService } from './metamask.service';
import { Injectable } from '@angular/core';
import { interval, Subject, BehaviorSubject, Observable } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { LocalStorageService } from './localstorage.service';

@Injectable({
  providedIn: 'root',
})
export class UserFacade {
  public accountBalance: AccountBalance = {} as AccountBalance;
  public tokenHistories: Observable<TokenHistoryResponse[]> = new Observable<TokenHistoryResponse[]>();
  public selectedDate: BehaviorSubject<Date> = new BehaviorSubject<Date>(
    new Date()
  );

  constructor(
    private web3Service: MetamaskService,
    private localService: LocalStorageService,
    private apiService: ApiService
  ) {
    this.web3Service
          .getAccountBalance(
            this.localService.getValue(LocalStorageKey.walletId)
          )
          .then((data: AccountBalance) => {
            console.log(data);
            this.accountBalance = data;
          });
  }

  public getTokenBalance(util$: Subject<void>) {
    interval(10000)
      .pipe(takeUntil(util$))
      .subscribe(() => {
        console.log('interval');
        this.web3Service
          .getAccountBalance(
            this.localService.getValue(LocalStorageKey.walletId)
          )
          .then((data: AccountBalance) => {
            console.log(data);
            this.accountBalance = data;
          });
      });
  }

  public getTokenHistories(util$: Subject<void>) {
    this.selectedDate.pipe(takeUntil(util$)).subscribe((date: Date) => {
      this.tokenHistories = this.apiService
        .getTokenHistories(date);
    });
  }

  public onNextDate() {
    var currentDate = this.selectedDate.value;
    var nextDate = currentDate.setDate(currentDate.getDate() + 1);
    this.selectedDate.next(new Date(nextDate));
  }

  public onPreviousDate() {
    var currentDate = this.selectedDate.value;
    var nextDate = currentDate.setDate(currentDate.getDate() - 1);
    this.selectedDate.next(new Date(nextDate));
  }

  private isToday(inputDate: Date) {
    var todaysDate = new Date();
    return inputDate.setHours(0, 0, 0, 0) == todaysDate.setHours(0, 0, 0, 0);
  }
}
