import { LoaderService } from './../../services/loader.service';
import { SharksFacade } from '../../services/sharks.facade';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-sharks',
  templateUrl: './sharks.component.html',
  styleUrls: ['./sharks.component.css', '../home/home.component.css'],
})
export class SharksComponent implements OnInit {
  play: boolean = false;
  buy: boolean = false;
  history: boolean = false;
  loading: boolean = false;
  claim: boolean = false;
  shark: boolean = false;

  rs: number = 0;
  stopRed: number = 0;
  move1: string = '';
  move2: string = '';
  move3: string = '';
  move4: string = '';
  move5: string = '';
  array: number[] = [0];
  count: number = 0;
  constructor(public facade: SharksFacade,public loader:LoaderService) {}

  ngOnInit(): void {
    this.count = 0;
    this.facade.getListSharks();
  }

  _move_random() {
    this.array = [];
    this.array.push(0);
    this.count = 0;

    for (let index = 1; this.count <= 5 && index < 100; index++) {
      let i = Math.round(Math.random() * 9) + 1;
      if (this.array.includes(i) == false) {
        this.array.push(i);
        this.count++;
      }
    }
    this.move1 = 'move' + this.array[1].toString();
    this.move2 = 'move' + this.array[2].toString();
    this.move3 = 'move' + this.array[3].toString();
    this.move4 = 'move' + this.array[4].toString();
    this.move5 = 'move' + this.array[5].toString();
  }
  confirm() {
    let text = 'Do you want to refuel your sharks for 0 $CSHARK?';
    if (confirm(text) == true) {
      // do something
    } else {
      // do something
    }
  }
  _quick_training() {
    let text = 'Are you sure you want to use quick training?';
    if (confirm(text) == true) {
      this.rs = 0;
      this.play = true;
      this.buy = false;
      this.history = false;
      this.loading = false;
      this.claim = false;
      this.shark = false;
      setTimeout(() => (this.rs = 1), 100);
    } else {
      // do something
    }
  }
  _play() {
    this.rs = 0;
    this.play = true;
    this.buy = false;
    this.history = false;
    this.loading = false;
    this.claim = false;
    this.shark = false;
    this._move_random();
    setTimeout(() => {
      this.rs = 1;
      this.stopRed = 1;
    }, 17000);
  }
  _buy() {
    this.play = false;
    this.buy = true;
    this.history = false;
    this.loading = false;
    this.claim = false;
    this.shark = false;
  }
  _history() {
    this.play = false;
    this.buy = false;
    this.history = true;
    this.loading = false;
    this.claim = false;
    this.shark = false;
  }
  _loading() {
    this.play = false;
    this.buy = false;
    this.history = false;
    this.loading = true;
    this.claim = false;
    this.shark = false;
  }
  _claim() {
    console.log('df');

    this.play = false;
    this.buy = false;
    this.history = false;
    this.loading = false;
    this.claim = true;
    this.shark = false;
  }
  _sharks() {
    this.play = false;
    this.buy = false;
    this.history = false;
    this.loading = false;
    this.claim = false;
    this.shark = true;
  }
  _close() {
    this.play = false;
    this.buy = false;
    this.history = false;
    this.loading = false;
    this.claim = false;
    this.shark = false;
  }
  _free() {
    this._loading();
    setTimeout(() => this._sharks(), 300);
  }
  createRange(number: number) {
    return new Array(number);
  }
}
