import { TokenAmountResponse } from './../models/accountBalance.model';
import { LocalStorageKey } from './../models/const';
import { LocalStorageService } from './localstorage.service';
import { LoginRequest, LoginResponse } from '../models/authenticate.model';
import { MetamaskService } from './metamask.service';
import { RequestLoginResponse } from '../models/requestlogin.model';
import { ApiService } from './api.service';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class HeaderFacade {
  public isAuthenticate: boolean = false;
  public currentAccount: TokenAmountResponse = {} as TokenAmountResponse;

  constructor(
    private api: ApiService,
    private web3Service: MetamaskService,
    private localService: LocalStorageService
  ) {
    if (localService.getValue(LocalStorageKey.token)) {
      this.api
        .getTokenAmount()
        .pipe()
        .subscribe((data) => {
          this.currentAccount = data;
          this.isAuthenticate = true;
          localService.setValue(LocalStorageKey.walletId, data.walletId);
        });
    }
  }

  public onHeaderInit(){
    if (this.localService.getValue(LocalStorageKey.token)) {
      this.api
        .getTokenAmount()
        .pipe()
        .subscribe((data) => {
          this.currentAccount = data;
          this.isAuthenticate = true;
          this.localService.setValue(LocalStorageKey.walletId, data.walletId);
        });
    }

    this.web3Service.currentAccount.subscribe((walletId) => {
      console.log('walletId', walletId);
      if(walletId==='init')
        return;
      if (!walletId) {
        this.isAuthenticate = false;
        return;
      }

      let request = {
        walletId: walletId,
      };

      this.api.requestLogin(request).subscribe((data: RequestLoginResponse) => {
        this.web3Service.signMessage(data).then((signature: string) => {
          let loginRequest: LoginRequest = {
            walletId: request.walletId,
            signature: signature,
          };
          this.api
            .authenticate(loginRequest)
            .subscribe((data: LoginResponse) => {
              this.isAuthenticate = true;
              this.currentAccount = data;
              this.localService.setValue(LocalStorageKey.walletId, data.walletId);
              this.localService.setValue(LocalStorageKey.token, data.token);
            });
        });
      });
    });
  }
  public connectMetaMask(): void {
    this.web3Service.openMetamask();
  }
  public getTokenAmount() {
    this.api.getTokenAmount().subscribe((data) => {
      this.currentAccount = data;
    });
  }
}
