import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'shortWallet',
})
export class ShortWalletPipe implements PipeTransform {
  transform(value: string, first: number = 4, last: number = 5): unknown {
    return value.substring(0, first) + '...' + value.substring(value.length - last);
  }
}
