import { ShortWalletPipe } from './pipes/short-wallet.pipe';
import { AppRoutingModule } from './app-routing.module';
import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { AppComponent } from './app.component';
import { APP_BASE_HREF, CommonModule } from '@angular/common';
import { HomeComponent } from './component/home/home.component';
import { SharksComponent } from './component/sharks/sharks.component';
import { UserComponent } from './component/user/user.component';
import { HistoryComponent } from './component/history/history.component';
import { HeaderComponent } from './component/header/header.component';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { FixedLengthPipe } from './pipes/fixed-length.pipe';
import { LoaderComponent } from './component/loader/loader.component';
import { CustomInterceptor } from './services/custom.interceptor';
@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    SharksComponent,
    UserComponent,
    HistoryComponent,
    HeaderComponent,
    ShortWalletPipe,
    FixedLengthPipe,
    LoaderComponent,
  ],
  imports: [AppRoutingModule, BrowserModule, CommonModule, HttpClientModule],
  providers: [
    { provide: APP_BASE_HREF, useValue: '/' },
    { provide: HTTP_INTERCEPTORS, useClass: CustomInterceptor, multi: true },
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
