import { LoaderService } from './../../services/loader.service';
import { Subject } from 'rxjs';
import { UserFacade } from './../../services/user.facade';
import { Component, OnDestroy, OnInit } from '@angular/core';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css', '../home/home.component.css'],
})
export class UserComponent implements OnInit, OnDestroy {
  public _destroy$: Subject<void> = new Subject<void>();
  
  constructor(public userFacade: UserFacade,public loader:LoaderService) {}

  ngOnInit(): void {
    this.userFacade.getTokenBalance(this._destroy$);
    this.userFacade.getTokenHistories(this._destroy$);
  }

  ngOnDestroy(): void {
    this._destroy$.next();
    this._destroy$.complete();
  }
}
