import { LoaderService } from './services/loader.service';
import { Component } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  title = 'app-game1';
  href: any;
  index: boolean = true;
  home: boolean = false;
  sharks: boolean = false;
  user: boolean = false;
  login: boolean = false;
  show: boolean = false;
  constructor(public loader:LoaderService) { }

  ngOnInit() {
    this.login =true;
  }
  play() {
    this.index = false;
    this.home = true;
    this.sharks = false;
    localStorage.setItem("home", "1");
  }

  _menudrop() {
    console.log(this.show);

    if (this.show == true) {
      this.show = false;
    } else {
      this.show = true;
    }
  }
}
