import { GameSettings } from './../models/gamesettings.model';
import { TokenAmountResponse } from './../models/accountBalance.model';
import { ListSharkResponse, Shark } from './../models/shark.model';
import { TokenHistoryResponse } from './../models/tokenHistory.model';
import { LocalStorageKey } from './../models/const';
import { LoginResponse } from '../models/authenticate.model';
import {
  RequestLoginRequest,
  RequestLoginResponse,
} from './../models/requestlogin.model';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { LoginRequest } from '../models/authenticate.model';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json',
  }),
};

const httpOptionsWithToken = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json',
    Authorization: `Bearer ${localStorage.getItem(LocalStorageKey.token)}`,
  }),
};

@Injectable({
  providedIn: 'root',
})
export class ApiService {
  private baseUrl: string = 'http://103.167.93.110/sharks/api/v1/';
  constructor(private client: HttpClient) {}

  public requestLogin(
    request: RequestLoginRequest
  ): Observable<RequestLoginResponse> {
    return this.client.post<RequestLoginResponse>(
      this.baseUrl + 'wallets/request-login',
      {
        ...request,
      },
      httpOptions
    );
  }

  public authenticate(request: LoginRequest): Observable<LoginResponse> {
    return this.client.post<LoginResponse>(
      this.baseUrl + 'wallets/login',
      {
        ...request,
      },
      httpOptions
    );
  }

  public getTokenHistories(date: Date): Observable<TokenHistoryResponse[]> {
    return this.client.get<TokenHistoryResponse[]>(
      this.baseUrl +
        `wallets/token-histories?date=${date.toLocaleDateString()}`,
      httpOptionsWithToken
    );
  }

  public getSharks(): Observable<Shark[]> {
    return this.client.get<Shark[]>(
      this.baseUrl + `wallets/sharks`,
      httpOptionsWithToken
    );
  }

  public getTokenAmount(): Observable<TokenAmountResponse> {
    return this.client.get<TokenAmountResponse>(
      this.baseUrl + `wallets/token-amount`,
      httpOptionsWithToken
    );
  }

  public openBox(): Observable<Shark> {
    return this.client.post<Shark>(
      this.baseUrl + `sharks/open-box`,
      {
        blindBoxType: 0,
      },
      httpOptionsWithToken
    );
  }

  public getGameSetting(): Observable<GameSettings> {
    return this.client.get<GameSettings>(
      this.baseUrl + `gamesettings`,
      httpOptions
    );
  }
}
